/* Effect 1: Brackets */
.cl-effect-1 {
  position: relative;
}

.cl-effect-1::before,
.cl-effect-1::after {
  display: inline-block;
  opacity: 0;
  transition: transform 0.3s, opacity 0.2s;
}

.cl-effect-1::before {
  margin-right: 10px;
  content: '[';
  transform: translateX(20px);
}

.cl-effect-1::after {
  margin-left: 10px;
  content: ']';
  transform: translateX(-20px);
}

.cl-effect-1:hover::before,
.cl-effect-1:hover::after,
.cl-effect-1:focus::before,
.cl-effect-1:focus::after {
  opacity: 1;
  transform: translateX(0px);
}

@media (max-width: 960px) {
  .open {
    position: absolute;
    top: 40px;
    right: 40px;
    width: 50px;
    height: 50px;
    display: block;
    cursor: pointer;
    transition: opacity 0.2s linear;
  }
  .open:hover {
    opacity: 0.8;
  }
  .open span {
    display: block;
    float: left;
    clear: both;
    height: 2px;
    width: 30px;
    background-color: #ea0000;
    position: absolute;
    right: 3px;
    top: 3px;
    overflow: hidden;
    transition: all 0.4s ease;
  }
  .open span:nth-child(1) {
    z-index: 9;
  }
  .open span:nth-child(2) {
    margin-top: 10px;
  }
  .open span:nth-child(3) {
    margin-top: 20px;
  }
  .sub-menu {
    transition: all 0.8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    height: 0;
    width: 0;
    right: 0;
    top: 0;
    position: absolute;
    background-color: rgba(34, 34, 34, 0.5);
    border-radius: 50%;
    z-index: 18;
    overflow: hidden;
  }
  .sub-menu .header__nav li {
    display: block;
    float: right;
    clear: both;
    height: auto;
    margin-right: -160px;
    transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  }
  .sub-menu .header__nav li:first-child {
    margin-top: 190px;
  }
  .sub-menu .header__nav li:nth-child(1) {
    -webkit-transition-delay: 0.05s;
  }
  .sub-menu .header__nav li:nth-child(2) {
    -webkit-transition-delay: 0.10s;
  }
  .sub-menu .header__nav li:nth-child(3) {
    -webkit-transition-delay: 0.15s;
  }
  .sub-menu .header__nav li:nth-child(4) {
    -webkit-transition-delay: 0.20s;
  }
  .sub-menu .header__nav li:nth-child(5) {
    -webkit-transition-delay: 0.25s;
  }
  .sub-menu .header__nav li a {
    color: #fff;
    width: 100%;
    display: block;
    float: left;
  }
  .oppenned .sub-menu {
    opacity: 1;
    height: 400px;
    width: 400px;
  }
  .oppenned span:nth-child(2) {
    overflow: visible;
  }
  .oppenned span:nth-child(1), .oppenned span:nth-child(3) {
    z-index: 100;
    transform: rotate(45deg);
  }
  .oppenned span:nth-child(1) {
    transform: rotate(45deg) translateY(10px) translateX(6px);
  }
  .oppenned span:nth-child(2) {
    height: 400px;
    width: 400px;
    right: -160px;
    top: -160px;
    border-radius: 50%;
    background-color: rgba(34, 34, 34, 0.5);
  }
  .oppenned span:nth-child(3) {
    transform: rotate(-45deg) translateY(-8px) translateX(4px);
  }
  .oppenned .header__nav__list li {
    margin-right: 135px;
  }
  .button {
    display: block;
    float: left;
    clear: both;
    padding: 20px 40px;
    background: #fff;
    border-radius: 3px;
    border: 2px solid #10a1ea;
    overflow: hidden;
    position: relative;
  }
  .button:after {
    transition: transform 0.3s ease;
    content: "";
    position: absolute;
    height: 200px;
    width: 400px;
    transform: rotate(45deg) translateX(-540px) translateY(-100px);
    background: #10a1ea;
    z-index: 1;
  }
  .button:before {
    transition: transform 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    content: attr(title);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    color: #fff;
    z-index: 2;
    text-align: center;
    padding: 20px 40px;
    transform: translateY(200px);
  }
  .button:hover {
    text-decoration: none;
  }
  .button:hover:after {
    transform: translateX(-300px) translateY(-100px);
  }
  .button:hover:before {
    transform: translateY(0);
  }
}

