.scheme-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.scheme-wrap .arrow-left img {
  transform: rotate(-90deg);
  transform-origin: center;
}

.scheme-wrap .arrow-btm img {
  transform: rotate(180deg);
  transform-origin: center;
}

.scheme-wrap .arrow-right img {
  transform: rotate(90deg);
  transform-origin: center;
}

.scheme-row {
  display: flex;
  align-items: center;
  margin: 20px 0;
}

.scheme {
  display: grid;
  grid-template-columns: 390px 390px;
  grid-template-rows: 250px 250px;
  grid-gap: 30px;
  margin: 0 20px;
}

.scheme-card {
  position: relative;
}

.scheme-card img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.scheme-card .card-info {
  width: 100%;
  height: 100%;
  position: relative;
  background: rgba(0, 0, 0, 0.5);
}

.scheme-card .card-name {
  position: absolute;
  left: 46px;
  bottom: 118px;
  color: #ffffff;
  font-family: 'Oswald', sans-serif;
  font-size: 32px;
  font-weight: 500;
  z-index: 2;
}

.scheme-card .card-link {
  background: #ea0000;
  width: 153px;
  height: 47px;
  border-radius: 24px;
  z-index: 2;
  position: absolute;
  bottom: 42px;
  left: 46px;
}

.scheme-card .card-link a {
  color: #ffffff;
  font-family: 'Oswald', sans-serif;
  font-size: 18px;
  font-weight: 600;
  width: 100%;
  height: 100%;
  display: inline-block;
  text-align: center;
  line-height: 47px;
}

.scheme-card:nth-child(1):before {
  right: 0;
}

.scheme-card:nth-child(1).next:before {
  height: calc(100% - 66px);
}

.scheme-card:nth-child(2):before {
  right: 0;
}

.scheme-card:nth-child(2).next:before {
  width: calc(100% - 80px);
}

.scheme-card:nth-child(4):before {
  bottom: 0;
}

.scheme-card:nth-child(4).next:before {
  height: calc(100% - 66px);
}

.scheme-card:nth-child(3).next:before {
  width: calc(100% - 80px);
}

